'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { 
  Server, 
  Plus, 
  ExternalLink,
  DollarSign,
  BarChart3,
  AlertCircle,
  Copy,
  Check
} from 'lucide-react'

// =============================================================================
// TYPES
// =============================================================================

interface Endpoint {
  id: string
  name: string
  upstreamUrl: string
  pricePerCall: string
  platformFeeBps: number
  status: string
  thisMonth?: {
    calls: number
    gross: string
    creatorNet: string
  }
}

// =============================================================================
// METERED ENDPOINTS PAGE
// =============================================================================

export default function MeteredEndpointsPage() {
  const [endpoints, setEndpoints] = useState<Endpoint[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [showCreate, setShowCreate] = useState(false)

  useEffect(() => {
    loadEndpoints()
  }, [])

  const loadEndpoints = async () => {
    try {
      setLoading(true)
      const apiKey = localStorage.getItem('api_key')
      if (!apiKey) throw new Error('No API key')

      const response = await fetch('/api/v1/endpoints', {
        headers: { 'x-api-key': apiKey },
      })
      const data = await response.json()
      
      if (!response.ok) throw new Error(data.error || 'Failed to load')
      
      // Load summary for each endpoint
      const endpointsWithStats = await Promise.all(
        (data.endpoints || []).map(async (ep: any) => {
          try {
            const summaryRes = await fetch(`/api/v1/endpoints/${ep.id}/summary`, {
              headers: { 'x-api-key': apiKey },
            })
            const summary = await summaryRes.json()
            return {
              ...ep,
              pricePerCall: ep.pricePerCall || ep.price_per_call || '0.001',
              platformFeeBps: ep.platformFeeBps || ep.platform_fee_bps || 200,
              thisMonth: summary.thisMonth,
            }
          } catch {
            return ep
          }
        })
      )
      
      setEndpoints(endpointsWithStats)
    } catch (err: any) {
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-3xl font-bold">Metered Endpoints</h1>
          <p className="text-gray-400 mt-1">Create endpoints with per-call pricing</p>
        </div>
        <button
          onClick={() => setShowCreate(true)}
          className="flex items-center gap-2 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300"
        >
          <Plus className="w-4 h-4" />
          Create Endpoint
        </button>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {loading ? (
        <LoadingState />
      ) : endpoints.length === 0 ? (
        <EmptyState onCreateClick={() => setShowCreate(true)} />
      ) : (
        <EndpointsList endpoints={endpoints} onRefresh={loadEndpoints} />
      )}

      {showCreate && (
        <CreateEndpointModal 
          onClose={() => setShowCreate(false)} 
          onCreated={() => {
            setShowCreate(false)
            loadEndpoints()
          }}
        />
      )}
    </div>
  )
}

// =============================================================================
// ENDPOINTS LIST
// =============================================================================

function EndpointsList({ endpoints, onRefresh }: { endpoints: Endpoint[]; onRefresh: () => void }) {
  return (
    <div className="space-y-4">
      {/* Header row */}
      <div className="grid grid-cols-12 gap-4 px-4 text-sm text-gray-500 font-medium">
        <div className="col-span-4">Endpoint</div>
        <div className="col-span-2">Price/Call</div>
        <div className="col-span-2">Calls (Month)</div>
        <div className="col-span-2">Revenue</div>
        <div className="col-span-2">Status</div>
      </div>

      {/* Endpoint rows */}
      {endpoints.map((endpoint) => (
        <EndpointRow key={endpoint.id} endpoint={endpoint} />
      ))}
    </div>
  )
}

function EndpointRow({ endpoint }: { endpoint: Endpoint }) {
  const [copied, setCopied] = useState(false)

  const copyEndpointUrl = () => {
    const url = `${window.location.origin}/api/v1/run/${endpoint.id}`
    navigator.clipboard.writeText(url)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  const platformFee = (parseFloat(endpoint.pricePerCall) * (endpoint.platformFeeBps / 10000)).toFixed(6)

  return (
    <div className="grid grid-cols-12 gap-4 p-4 bg-gray-900 rounded-lg border border-gray-800 items-center">
      {/* Name + URL */}
      <div className="col-span-4">
        <div className="font-medium text-white">{endpoint.name}</div>
        <div className="text-sm text-gray-500 truncate">{endpoint.upstreamUrl}</div>
        <button
          onClick={copyEndpointUrl}
          className="mt-1 text-xs text-lime-400 hover:text-lime-300 flex items-center gap-1"
        >
          {copied ? <Check className="w-3 h-3" /> : <Copy className="w-3 h-3" />}
          {copied ? 'Copied!' : 'Copy metered URL'}
        </button>
      </div>

      {/* Price */}
      <div className="col-span-2">
        <div className="text-white font-mono">${endpoint.pricePerCall}</div>
        <div className="text-xs text-gray-500">
          Fee: ${platformFee} ({endpoint.platformFeeBps / 100}%)
        </div>
      </div>

      {/* Calls */}
      <div className="col-span-2">
        <div className="text-white font-mono">
          {endpoint.thisMonth?.calls?.toLocaleString() || '0'}
        </div>
      </div>

      {/* Revenue */}
      <div className="col-span-2">
        <div className="text-lime-400 font-mono">
          ${endpoint.thisMonth?.creatorNet || '0.00'}
        </div>
        <div className="text-xs text-gray-500">
          Gross: ${endpoint.thisMonth?.gross || '0.00'}
        </div>
      </div>

      {/* Status */}
      <div className="col-span-2">
        <span className={`px-2 py-1 text-xs rounded ${
          endpoint.status === 'active' 
            ? 'bg-green-900/30 text-green-400' 
            : 'bg-gray-800 text-gray-400'
        }`}>
          {endpoint.status}
        </span>
      </div>
    </div>
  )
}

// =============================================================================
// EMPTY STATE
// =============================================================================

function EmptyState({ onCreateClick }: { onCreateClick: () => void }) {
  return (
    <div className="text-center py-16 bg-gray-900 rounded-lg border border-gray-800">
      <Server className="w-16 h-16 text-gray-600 mx-auto mb-4" />
      <h3 className="text-xl font-semibold mb-2">No metered endpoints yet</h3>
      <p className="text-gray-400 mb-6 max-w-md mx-auto">
        Create a metered endpoint to start charging per API call.
        Set your price, and we'll handle the billing.
      </p>
      <button
        onClick={onCreateClick}
        className="px-6 py-3 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300"
      >
        Create Your First Endpoint
      </button>
    </div>
  )
}

function LoadingState() {
  return (
    <div className="text-center py-12">
      <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-lime-400"></div>
      <p className="mt-4 text-gray-400">Loading endpoints...</p>
    </div>
  )
}

// =============================================================================
// CREATE ENDPOINT MODAL
// =============================================================================

function CreateEndpointModal({ 
  onClose, 
  onCreated 
}: { 
  onClose: () => void
  onCreated: () => void 
}) {
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [formData, setFormData] = useState({
    name: '',
    upstreamUrl: '',
    pricePerCall: '0.001',
    platformFeeBps: 200,
    recipientAddress: '',
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)

    try {
      const apiKey = localStorage.getItem('api_key')
      if (!apiKey) throw new Error('No API key')

      const response = await fetch('/api/v1/endpoints', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-api-key': apiKey,
        },
        body: JSON.stringify({
          name: formData.name,
          upstreamUrl: formData.upstreamUrl,
          price: '1000000', // Legacy field
          recipientAddress: formData.recipientAddress,
          pricePerCall: formData.pricePerCall,
          platformFeeBps: formData.platformFeeBps,
        }),
      })

      const data = await response.json()
      if (!response.ok) throw new Error(data.error || 'Failed to create endpoint')

      onCreated()
    } catch (err: any) {
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-gray-900 rounded-xl border border-gray-800 p-6 max-w-lg w-full mx-4">
        <h2 className="text-2xl font-bold mb-6">Create Metered Endpoint</h2>

        {error && (
          <div className="mb-4 p-3 bg-red-900/20 border border-red-800 rounded-lg text-red-400 text-sm">
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-1">
              Endpoint Name *
            </label>
            <input
              type="text"
              required
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              placeholder="My API Endpoint"
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:border-lime-400 focus:outline-none"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-300 mb-1">
              Upstream URL *
            </label>
            <input
              type="url"
              required
              value={formData.upstreamUrl}
              onChange={(e) => setFormData({ ...formData, upstreamUrl: e.target.value })}
              placeholder="https://api.example.com/v1/endpoint"
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:border-lime-400 focus:outline-none"
            />
            <p className="text-xs text-gray-500 mt-1">
              The actual API endpoint that will be called
            </p>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-300 mb-1">
                Price per Call (USDC) *
              </label>
              <input
                type="text"
                required
                value={formData.pricePerCall}
                onChange={(e) => setFormData({ ...formData, pricePerCall: e.target.value })}
                placeholder="0.001"
                className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:border-lime-400 focus:outline-none"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-300 mb-1">
                Platform Fee (%)
              </label>
              <input
                type="number"
                min="0"
                max="100"
                step="0.1"
                value={formData.platformFeeBps / 100}
                onChange={(e) => setFormData({ ...formData, platformFeeBps: parseFloat(e.target.value) * 100 })}
                className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:border-lime-400 focus:outline-none"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-300 mb-1">
              Your Wallet Address *
            </label>
            <input
              type="text"
              required
              value={formData.recipientAddress}
              onChange={(e) => setFormData({ ...formData, recipientAddress: e.target.value })}
              placeholder="0x..."
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white font-mono text-sm focus:border-lime-400 focus:outline-none"
            />
            <p className="text-xs text-gray-500 mt-1">
              Where you'll receive payments (minus platform fee)
            </p>
          </div>

          <div className="flex gap-3 pt-4">
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 disabled:opacity-50"
            >
              {loading ? 'Creating...' : 'Create Endpoint'}
            </button>
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

